.gantt_task_line {
  background-color: #1b466b;
  border-color: #051f34;
}

/* Adjust link colours */
.gantt_line_wrapper div {
  background-color: #137cbd;
}

.gantt_link_arrow_left {
  border-right-color: #137cbd;
}

.gantt_link_arrow_right {
  border-left-color: #137cbd;
}

.gantt_task_link:hover .gantt_line_wrapper div {
  box-shadow: 0 0 5px 0 #137cbd;
}

/* Adjust Link target colours */
.link_finish_allow .gantt_link_control.task_end_date div,
.link_start_allow .gantt_link_control.task_start_date div {
  background-color: #4fa4da;
  border-color: #137cbd;
}

/* Adjust pop-up form colours */
.gantt_btn_set.gantt_save_btn_set {
  background: #137cbd;
  text-shadow: none;
}
.gantt_btn_set.gantt_delete_btn_set {
  background: #db3737;
  text-shadow: none;
}
.gantt_cal_ltitle {
  background-color: #1b466b;
  color: white;
}
.gantt_cal_ltext > textarea {
  padding: 5px;
}
.gantt_save_btn {
  display: none;
}

.gantt_btn_set div {
  margin-left: 5px;
}
.gantt_cancel_btn {
  display: none;
}
.gantt_delete_btn {
  display: none;
}
/* Adjust delete confirm colours */
.gantt_popup_button.gantt_ok_button {
  background: #db3737;
  text-shadow: none;
}

/* Adjust hover over tasks and select task colours */
.gantt_grid_data .gantt_row.odd:hover,
.gantt_grid_data .gantt_row:hover {
  background-color: #137cbd;
}
.gantt_grid_data .gantt_row.odd:hover > .gantt_cell,
.gantt_grid_data .gantt_row:hover > .gantt_cell {
  color: white;
}

.gantt_grid_data .gantt_row.gantt_selected,
.gantt_grid_data .gantt_row.odd.gantt_selected,
.gantt_task_row.gantt_selected {
  background-color: #137cbd;
}
.gantt_grid_data .gantt_row.gantt_selected > .gantt_cell,
.gantt_grid_data .gantt_row.odd.gantt_selected > .gantt_cell,
.gantt_task_row.gantt_selected > .gantt_cell {
  color: white;
}

/* Adjust width of (invisible atm) tree heirarchy */
.gantt_tree_icon.gantt_blank {
  width: "10px";
}
.gantt_row.odd {
  background-color: inherit;
}

.darkGantt {
  background-color: #30404d !important;
}

.darkGanttText {
  color: white !important;
}

.gantt_add {
  display: none;
  width: 0px;
}
.gantt_grid_head_cell.gantt_grid_head_add {
  opacity: 0.9;
}
